export default {
	props:{
		storeData:{
			type:Array,
			default:[
				{
					id:1,
					name:'门店1号',
					address:'厦门市思明区汇景商业广场XXX1号',
					tel:'12345678900',
				},{
					id:2,
					name:'门店2号',
					address:'厦门市思明区汇景商业广场XXX2号',
					tel:'12345678900',
				},
			]
		},
		 markers:{
			 type:Array,
			 default: [
			{
				id:1,
				latitude: 22.656830122431952,
				longitude: 114.01990361901852,
				iconPath: '../../static/hch-position/门店.png',
				callout:{
					content:"门店1号店",
					borderRadius:10,
					padding:10,
					display:"ALWAYS",
				}
			}, {
				id:2,
				latitude: 22.663465,
				longitude: 114.023343,
				iconPath: '../../static/hch-position/门店.png',
				callout:{
					content:"门店2号店",
					borderRadius:10,
					padding:10,
					display:"ALWAYS",
				}
			}]
		 },
	},
    data() {
        return {
			store:{},
			storeFlag:false,
			storeAdFlag:true,
			distanceL:0,//附近店门的距离
            latitude: 22.663465,
            longitude: 114.023343,
			controls:[
				
			]
        }
    },
    methods: {
		gotoupersoncenter(){
			uni.navigateTo({
				url:'../../pages/personcenter/personcenter'
			})
			//uni.showToast({title:'跳到个人中心',duration:2000,icon:'none'});
		},
		scanCode(){
			uni.scanCode({
				success:function(e){
					console.log(e,'扫码成功返回')
				},fail:function(e){
					console.log(e,'扫码失败返回')
				}
			})
		},
		// 点击了门店信息进行导航
		storeDesEvn(){
			//uni.showToast({title:'自己自定义跳门店详情页介绍',duration:2000,icon:'none'});
			uni.getLocation({
			    type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			    success: function (res) {
			        const latitude = res.latitude;
			        const longitude = res.longitude;
			        uni.openLocation({
			            latitude: latitude,
			            longitude: longitude,
			            success: function () {
			                console.log('success');
			            }
			        });
			    }
			});
		},
		barkpostion(){
			let t=this;
			var mapCtx = wx.createMapContext('myMap',this);
			mapCtx.moveToLocation();
		},
		// 点击气泡
		callouttap(e){
			this.storeFlag=true;
			this.storeAdFlag=false;
			for (let i = 0; i < this.storeData.length; i++) {
				const ele = this.storeData[i];
				if(ele.id==e.markerId){
					this.store = ele;
					break;
				}
			}
			
		},
		// 点击地图
		bindtap(id){
			this.storeFlag=false;
			console.log(this.storeFla,'点击了')
			// this.storeAdFlag=true;
		},
		// 隐藏广告图片
		hideAd(){
			this.storeAdFlag=false;
		},
		updated(){
			let _this = this;
			wx.getLocation({
			  type: 'gcj02', //返回可以用于wx.openLocation的经纬度
			  success: function (res) {
				_this.latitude = res.latitude;
				_this.longitude = res.longitude;
				_this.nearDistance(_this.markers,_this.latitude,_this.longitude)
			  },
			   fail: function(res){
				   console.log(res);
			   }
		  })
		},
		// 改变视野时改变经纬度
		regionchange(e){
			let _this = this;
			// 使用 wx.createMapContext 获取 map 上下文
			var mapCtx = wx.createMapContext('myMap',this)
			mapCtx.getScale({
				success:function(e){
					//console.log(e,'当前比例')
				}
			})
			mapCtx.getCenterLocation({
				success:function(res){
					_this.centerLatitude = res.latitude;
					_this.centerLongitude = res.longitude;
					_this.nearDistance(_this.markers,_this.centerLatitude,_this.centerLongitude)
				},
				fail: function(res){
				   console.log(res);
				}
			}) //获取当前地图的中心经纬度
		},
		// 两点间距离
		distance(la1, lo1, la2, lo2) {
			var La1 = la1 * Math.PI / 180.0;
			var La2 = la2 * Math.PI / 180.0;
			var La3 = La1 - La2;
			var Lb3 = lo1 * Math.PI / 180.0 - lo2 * Math.PI / 180.0;
			var s = 2 * Math.asin(Math.sqrt(Math.pow(Math.sin(La3 / 2), 2) + Math.cos(La1) * Math.cos(La2) * Math.pow(Math.sin(Lb3 / 2), 2)));
			s = s * 6378.137;//地球半径
			s = Math.round(s * 10000) / 10000;
			return s
		},
		// 计算最近的距离
		nearDistance(array,centerLatitude,centerLongitude){
			let temp=[]
			for (let i = 0,l=array.length; i < l; i++) {
				const element =  array[i];
				let d = this.distance(element.latitude,element.longitude,centerLatitude,centerLongitude);
				temp.push(d)
			};
			this.distanceL = Math.min.apply(null, temp);
		}
		
		
    },
	onShow() {
		
	},
	onReady() {
		
  },
}
